<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="试卷名称" prop="paperid" >
					<el-select v-model="ruleForm.paperid" placeholder="试卷名称" @change="paperChange">
						<el-option v-for="(item,index) in paperlist" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="试卷名称" prop="papername" >
					<el-input v-model="ruleForm.papername" placeholder="试卷名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="分数" prop="score" >
					<el-input v-model.number="ruleForm.score" placeholder="分数" clearable  :readonly="ro.score"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="分数" prop="score" >
					<el-input v-model="ruleForm.score" placeholder="分数" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="建议" prop="determine" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder="建议"
					v-model="ruleForm.determine" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.determine" label="建议" prop="determine" >
				<span class="text">{{ruleForm.determine}}</span>
			</el-form-item>
			<el-form-item class="textarea" v-if="type!='info'" label="分析" prop="analysis" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder="分析"
					v-model="ruleForm.analysis" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.analysis" label="分析" prop="analysis" >
				<span class="text">{{ruleForm.analysis}}</span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

